    background-color:#FE724E;
    border-radius: 20px;
    width: 150px;
    height: 40px;
    outline:none;
    border: none;
    text-align: center;
    color: #fff;
    cursor: pointer;
}

.nav-button a{
    text-decoration: none;
    color: #fff;
}


.hamburger{
    font-size: 34px;
    cursor:pointer;
    display: none;
}


