.name {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 30px 0;
}

.fname input {
  margin: 0 10px 0 0;
  padding: 10px;
  width: 200px;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  background: transparent;
}

.lname input {
  margin: 0 0 0 20px;
  padding: 10px;
  width: 200px;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  background: transparent;
}

.email {
  padding: 30px 0;
}

.email input {
  width: 450px;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  padding: 10px;
  background: transparent;
}

.mext {
  padding: 30px 0;
}

.message {
  width: 450px;
  height: 70px;
  outline: none;
  border: none;
  border-bottom: 1px solid #000;
  padding: 10px;
  background: transparent;
}

.form-button {
  background-color: #ffc42b;
  color: white;
  outline: none;
  border-radius: 30px;
  height: 50px;
  width: 200px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
}

@media (max-width: 1140px) {
  .fname input,
  .lname input {
    margin: 0 0px 0 0;
    width: 150px;
  }

  .email input, .message {
    width: 350px;
  }

  .email, .mext {
    padding: 30px 50px;
  }
  
  .form-button { 
    width: 150px;
    font-size: 15px;
    margin: 0 50px;
  }

}

@media (max-width: 900px){
  .name {
    display: block;
   padding: 0px;
  }

  .fname,.lname{
    padding: 30px 0;
  }

  .fname input,
  .lname input {
    margin: 0 0px 0 0;
    width: 350px;
  }

  form{
    margin: auto;
    width: 100%;
  }
  .email, .mext {
    padding: 30px 0px;
  }
}

